<template>
    <div>
        <el-button class="change" @click="change">修改</el-button>
        <el-button class="change" @click="delete_">删除</el-button>
        <AddApi api="/change/" :data="data" v-if="status"></AddApi>
        <div v-else class="item">
            <h3>{{data.title}}</h3>
            <h4>url: {{data.url}}</h4>
            <p>method: {{data.type}}</p>
            <p>login_require: {{data.login_require}}</p>
            <p v-if="data.description">description: {{data.description}}</p>
            <div v-if="data.send.length">
                <p>Send:</p>
                <p v-for="(item,index) in data.send" :key="'s' + index">&emsp;{{item.name}}: {{item.type}}</p>
            </div>
            <div v-if="data.receive.length">
                <p>Receive:</p>
                <p v-for="(item,index) in data.receive" :key="'r' + index">&emsp;{{item.name}}: {{item.type}}</p>
            </div>
            <p class="base">
                <el-input placeholder="Base Url" v-model="base_url"></el-input>
            </p>
            <el-button @click="generate" class="gene">快速生成axios请求</el-button>
            <el-input type="textarea" v-if="req" v-model="req" :autosize="{ minRows: 2}">
            </el-input>
        </div>
    </div>
</template>

<script>
    import AddApi from "./AddApi"

    export default {
        props: ["data"],
        name: "ApiItem",
        components: {
            AddApi
        },
        data() {
            return {
                req: "",
                base_url: "this.$bus.base_url",
                status: false
            }
        },
        methods: {
            change() {
                this.status = !this.status;
            },
            delete_() {
                this.axios.post(this.$bus.base_url + "/delete/", {
                    id: this.data.id
                }).then((resp) => {
                    if (resp.data.status) {
                        this.$message.success("删除成功");
                        location.reload();
                    } else {
                        this.$message.error("删除失败: " + resp.data.error)
                    }
                })
            },
            generate() {
                let s;
                if (this.base_url.startsWith("http")) {
                    s = "this.axios." + this.data.type.toLocaleLowerCase() + "(\"" + this.base_url + this.data.url;
                } else {
                    s = "this.axios." + this.data.type.toLocaleLowerCase() + "( + " + this.base_url + " + \"" + this.data.url;
                }
                if (this.data.type === "GET") {
                    s += "?";
                    let qs = [];
                    for (let i = 0; i < this.data.send.length; i++) {
                        let q = this.data.send[i].name + "=\"" + "+this." + this.data.send[i].name;
                        qs.push(q);
                    }
                    s += qs.join("+\"&");
                    s += ")"
                } else {
                    s += "\", {\n";
                    let qs = [];
                    for (let i = 0; i < this.data.send.length; i++) {
                        let q = "    " + this.data.send[i].name + ": " + "this." + this.data.send[i].name;
                        qs.push(q);
                    }
                    s += qs.join(",\n");
                    s += "\n})"
                }
                s += "\n    .then(resp=>{\n        let data = resp.data;\n";
                let qs = [];
                for (let i = 0; i < this.data.receive.length; i++) {
                    let q = "        " + "this." + this.data.receive[i].name + " = data." + this.data.receive[i].name;
                    qs.push(q);
                }
                s += qs.join(";\n");
                s += "\n})";
                this.req = s;
            }
        }
    }
</script>

<style scoped>
    .item {
        width: 90%;
        box-shadow: 0 0 10px lightgray;
        padding: 10px 5%;
        margin: 10px 0;
        text-align: left;
    }

    .base {
        width: 55%;
        display: inline-block;
        margin-right: 5%;
    }

    .gene {
        display: inline;
    }

    .change {
        float: right;
        margin: 20px;
    }
</style>